<template>
    <section>
        <!--工具条-->
        <el-row :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-col :span="12">
                <el-form :inline="true" :model="filters" class="grid-content bg-purple">
                    <el-form-item>
                        <el-tooltip class="item" effect="dark" content="根据药品名称、柜台编号、柜台名称搜索" placement="right">
                            <el-input v-model="filters.keyword" placeholder="关键字"></el-input>
                        </el-tooltip>
                    </el-form-item>
                </el-form>
            </el-col>
            <!--            <el-col :span="1" :offset="10">-->
            <!--                <el-form class="grid-content bg-purple-light">-->
            <!--                    <el-form-item>-->
            <!--                        <el-button type="primary" @click="handleAdd">添加药品</el-button>-->
            <!--                    </el-form-item>-->
            <!--                </el-form>-->
            <!--            </el-col>-->
        </el-row>

        <!--列表-->
        <el-table :data="drugShowList" highlight-current-row v-loading="table.loading" element-loading-text="拼命加载中"
                  @selection-change="selsChange" style="width: 100%;">
            <el-table-column type="index" width="60">
            </el-table-column>
            <el-table-column prop="drugName" label="药品名称" sortable>
            </el-table-column>
            <el-table-column prop="showId" label="柜台编号" sortable>
            </el-table-column>
            <el-table-column prop="showName" label="柜台名称" sortable>
            </el-table-column>
            <el-table-column prop="drugPlace" label="摆放编号" sortable>
            </el-table-column>
            <el-table-column prop="creator" label="创建人" sortable>
            </el-table-column>
            <el-table-column prop="createTime" label="创建时间" sortable>
            </el-table-column>
            <el-table-column prop="updateUser" label="修改人" sortable>
            </el-table-column>
            <el-table-column prop="updateTime" label="修改时间" sortable>
            </el-table-column>
            <el-table-column label="操作" min-width="120">
                <template scope="scope">
                    <el-button size="small" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                    <!--                    <el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)">删除</el-button>-->
                </template>
            </el-table-column>
        </el-table>

        <!--工具条-->
        <el-col :span="24" class="toolbar" style="padding-bottom:10px;">
            <!--分页-->
            <el-pagination
                    @current-change="handleCurrentChange"
                    @size-change="handleSizeChange"
                    :current-page="table.page" :page-sizes="[10, 25, 50]" :page-size="table.size"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="drugShowList.length" style="float:right">
            </el-pagination>
        </el-col>

        <!--新增、编辑界面-->
        <el-dialog :title="form.title" v-model="form.visible" :close-on-click-modal="false">
            <el-form :model="form.data " label-width="100px" :rules="form.rules" ref="form">
                <el-form-item label="药品名称" prop="drugName">
                    <el-input v-model="form.data.drugName" :disabled="true" auto-complete="off"
                              placeholder="药品名称"></el-input>
                </el-form-item>
                <el-form-item label="柜台编号" prop="showId">
                    <el-input v-model="form.data.showId" auto-complete="off" placeholder="柜台编号"></el-input>
                </el-form-item>
                <el-form-item label="柜台名称" prop="showName">
                    <el-input v-model="form.data.showName" placeholder="柜台名称"></el-input>
                </el-form-item>
                <el-form-item label="摆放编号" prop="drugPlace">
                    <el-input v-model="form.data.drugPlace" placeholder="摆放编号"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="form.visible = false">取 消</el-button>
                <!--<el-button type="danger" @click="handleDel(form.data)" v-show="form.data.id != ''">删除</el-button>-->
                <el-button type="primary" @click.native="submit" :loading="form.loading">
                    <div>{{form.btnText}}</div>
                </el-button>
            </div>
        </el-dialog>

    </section>
</template>

<script>
    import {getDrugShowList, editDrugShow} from '../../api/api'

    export default {
        data() {
            return {
                filters: {
                    keyword: ''
                },
                sels: [],//列表选中列
                userName: '',
                table: {
                    loading: false,
                    page: 1,
                    size: 10,
                    data: []
                },
                form: {
                    title: '',
                    visible: false,//界面是否显示
                    loading: false,
                    btnText: '提交',
                    data: {
                        id: '',
                        drugId: '',
                        drugName: '',
                        showId: '',
                        showName: '',
                        drugPlace: '',
                        creator: '',
                        createTime: '',
                        updateUser: '',
                        updateTime: ''
                    },
                    rules: {
                        drugName: [
                            {required: true, message: '请输入药品名称', trigger: 'blur'}
                        ]
                    }
                }
            };
        },
        computed: {
            drugShowList: function () {
                var self = this, data = self.table.data, page = self.table.page, size = self.table.size,
                    keyword = self.filters.keyword;
                var v = data.filter(function (p) {
                    return (p.drugName && p.drugName.indexOf(keyword) !== -1)
                        || (p.showId && p.showId.indexOf(keyword) !== -1)
                        || (p.showName && p.showName.indexOf(keyword) !== -1)
                });
                v = v.slice((page - 1) * size, page * size);
                return v;
            }
        },
        methods: {
            getDrugShowList() {
                this.table.loading = true;
                getDrugShowList().then((data) => {
                    this.table.data = data;
                    this.table.loading = false;
                })
            },
            selsChange: function (sels) {
                this.sels = sels;
            },
            handleCurrentChange(val) {
                this.table.page = val;
            },
            handleSizeChange(val) {
                this.table.size = val;
            },
            handleEdit: function (index, row) {
                this.form.visible = true;
                this.form.title = '编辑柜台信息';
                this.form.data.id = row.id;
                this.form.data.drugId = row.drugId;
                this.form.data.drugName = row.drugName;
                this.form.data.showId = row.showId;
                this.form.data.showName = row.showName;
                this.form.data.drugPlace = row.drugPlace;
                this.form.data.createTime = row.createTime;
                this.form.data.creator = row.creator;
                this.form.data.updateTime = row.updateTime;
                this.form.data.updateUser = this.userName;
            },
            submit: function () {
                this.$refs.form.validate((valid) => {
                    if (valid) {
                        this.$confirm('确认提交吗?', '提示', {}).then(() => {
                            this.form.loading = true;
                            this.form.btnText = '提交中';
                            var formData = this.form.data;
                            let param = Object.assign({}, formData);
                            editDrugShow(param).then((data) => {
                                let {message, type} = data;
                                this.$message({
                                    showClose: true,
                                    message: message,
                                    type: type
                                });
                                this.getDrugShowList();
                            });
                            this.form.loading = false;
                            this.form.btnText = '提交';
                            this.form.visible = false;
                        });
                    }
                });
            },
            //获取当前用户名
            getUserName: function () {
                var user = sessionStorage.getItem('user');
                if (user) {
                    user = JSON.parse(user);
                    this.userName = user.account;
                }
            },
        },
        mounted() {
            this.getUserName();
            this.getDrugShowList();
        }
    }

</script>


