<template>
    <section>
        <!--工具条-->
        <el-row :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-col :span="12">
                <el-form :inline="true" :model="filters">
                    <el-form-item>
                        <el-tooltip class="item" effect="dark" content="根据药品名称、操作人搜索" placement="right">
                            <el-input v-model="filters.keyword" placeholder="关键字"></el-input>
                        </el-tooltip>
                    </el-form-item>
                </el-form>
            </el-col>
            <el-col :span="1" :offset="7">
                <el-form class="grid-content bg-purple-light">
                    <div class="block">
                        <el-date-picker
                                v-model="range"
                                type="daterange"
                                unlink-panels
                                @change="handleChange"
                                range-separator="至"
                                start-placeholder="开始日期"
                                end-placeholder="结束日期">
                        </el-date-picker>
                    </div>
                </el-form>
            </el-col>
        </el-row>

        <!--列表-->
        <template>
            <el-table :data="sellHistoryList" show-summary highlight-current-row v-loading="table.loading"
                      style="width: 100%;">
                <el-table-column type="index" width="60">
                </el-table-column>
                <!--                <el-table-column prop="projectName" @click="publishLog" label="项目名称" sortable>-->
                <!--                    <template scope="scope">-->
                <!--                        <router-link :to="'/log/'+scope.row.id">{{ scope.row.projectName }}</router-link>-->
                <!--                    </template>-->
                <!--                </el-table-column>-->
                <el-table-column prop="drugName" label="药品名称" sortable>
                </el-table-column>
                <el-table-column prop="retailPrice" label="单价" sortable>
                </el-table-column>
                <el-table-column prop="number" label="销售数量" sortable>
                </el-table-column>
                <el-table-column prop="totalPrice" label="总计" sortable>
                </el-table-column>
                <el-table-column prop="creator" label="操作人" sortable>
                </el-table-column>
                <el-table-column prop="createTime" label="出售时间" sortable>
                </el-table-column>
                <el-table-column prop="statement" label="说明" sortable>
                </el-table-column>
            </el-table>

            <!--工具条-->
            <el-col :span="24" class="toolbar" style="padding-bottom:10px;">
                <!--分页-->
                <el-pagination
                        @current-change="handleCurrentChange"
                        @size-change="handleSizeChange"
                        :current-page="table.page" :page-sizes="[10, 25, 50]" :page-size="table.size"
                        layout="total, sizes, prev, pager, next, jumper"
                        :total="table.data.length" style="float:right">
                </el-pagination>
            </el-col>
        </template>

    </section>
</template>
<script>
    import {getSellHistory,getSellHistoryByTime} from '../../api/api';

    export default {
        data() {
            return {
                range: '',
                start: '',
                end: '',
                filters: {
                    keyword: ''
                },
                table: {
                    loading: false,
                    page: 1,
                    size: 10,
                    data: []
                }
            }
        },
        computed: {
            sellHistoryList: function () {
                var self = this, data = self.table.data, page = self.table.page, size = self.table.size,
                    keyword = self.filters.keyword;
                var v = data.filter(function (p) {
                    return (p.drugName && p.drugName.indexOf(keyword) !== -1)
                        || (p.creator && p.creator.indexOf(keyword) !== -1)
                });
                v = v.slice((page - 1) * size, page * size);
                return v;
            }
        },
        methods: {
            publishHistoryList() {
                this.table.loading = true;
                getSellHistory().then((data) => {
                    this.table.data = data;
                    this.table.loading = false;
                })
            },
            handleCurrentChange(val) {
                this.table.page = val;
            },
            handleSizeChange(val) {
                this.table.size = val;
            },
            handleChange: function (value) {
                let splits = value.split("至");
                this.start = splits[0];
                this.end = splits[1];
                getSellHistoryByTime(JSON.stringify(value)).then((data) => {
                    this.table.data = data;
                });
            },
        },
        mounted() {
            this.publishHistoryList();
        }
    };

</script>

<style scoped>

</style>


