<template>
    <section>
        <!--工具条-->
        <el-row :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-col :span="12">
                <el-form :inline="true" :model="filters" class="grid-content bg-purple">
                    <el-form-item>
                        <el-tooltip class="item" effect="dark" content="根据药品搜索" placement="right">
                            <el-input v-model="filters.keyword" placeholder="关键字"></el-input>
                        </el-tooltip>
                    </el-form-item>
                </el-form>
            </el-col>
            <el-col :span="1" :offset="10">
                <el-form class="grid-content bg-purple-light">
                    <el-form-item>
                        <el-button type="primary" @click="handleAdd">新建退货单</el-button>
                    </el-form-item>
                </el-form>
            </el-col>
        </el-row>

        <!--列表-->
        <el-table :data="drugReturnList" highlight-current-row v-loading="table.loading" element-loading-text="拼命加载中"
                  @selection-change="selsChange" style="width: 100%;">
            <el-table-column type="index" width="60">
            </el-table-column>
            <el-table-column prop="drugName" label="药品名称" sortable>
            </el-table-column>
            <el-table-column prop="number" label="退货数量" sortable>
            </el-table-column>
            <el-table-column prop="sellTime" label="出售时间" sortable>
            </el-table-column>
            <el-table-column prop="cause" label="退货原因" sortable>
            </el-table-column>
            <el-table-column prop="creator" label="创建人" sortable>
            </el-table-column>
            <el-table-column prop="createTime" label="创建时间" sortable>
            </el-table-column>
            <!--            <el-table-column prop="updateUser" label="修改人" sortable>-->
            <!--            </el-table-column>-->
            <!--            <el-table-column prop="updateTime" label="修改时间" sortable>-->
            <!--            </el-table-column>-->
            <!--            <el-table-column label="操作" min-width="120">-->
            <!--                <template scope="scope">-->
            <!--                    <el-button size="small" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>-->
            <!--                    <el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)">删除</el-button>-->
            <!--                </template>-->
            <!--            </el-table-column>-->
        </el-table>

        <!--工具条-->
        <el-col :span="24" class="toolbar" style="padding-bottom:10px;">
            <!--分页-->
            <el-pagination
                    @current-change="handleCurrentChange"
                    @size-change="handleSizeChange"
                    :current-page="table.page" :page-sizes="[10, 25, 50]" :page-size="table.size"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="drugReturnList.length" style="float:right">
            </el-pagination>
        </el-col>

        <!--新增、编辑界面-->
        <el-dialog :title="form.title" v-model="form.visible" :close-on-click-modal="false">
            <el-form :model="form.data " label-width="100px" :rules="form.rules" ref="form">
                <el-form-item label="药品名称" prop="name">
                    <!--                    <el-input v-model="form.data.name" auto-complete="off" placeholder="药品名称"></el-input>-->
                    <template>
                        <el-select v-model="form.data.drugId" filterable placeholder="请选择">
                            <el-option
                                    v-for="item in form.select"
                                    :key="item.id"
                                    :label="item.name"
                                    :value="item.id">
                            </el-option>
                        </el-select>
                    </template>
                </el-form-item>
                <el-form-item label="退货数量" prop="number">
                    <el-input-number v-model="form.data.number" :min="1"></el-input-number>
                    <!--                    <el-input v-model="form.data.number" auto-complete="off" placeholder="退货数量"></el-input>-->
                </el-form-item>
                <el-form-item label="出售时间" prop="sellTime">
                    <div class="block">
                        <el-date-picker
                                v-model="form.data.sellTime"
                                align="right"
                                type="date"
                                placeholder="选择日期"
                                :picker-options="pickerOptions">
                        </el-date-picker>
                    </div>
                </el-form-item>
                <el-form-item label="原因" prop="cause">
                    <el-input type="textarea" v-model="form.data.cause" auto-complete="off"
                              placeholder="退货原因"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="form.visible = false">取 消</el-button>
                <el-button type="primary" @click.native="submit" :loading="form.loading">
                    <div>{{form.btnText}}</div>
                </el-button>
            </div>
        </el-dialog>

    </section>
</template>

<script>
    import {getDrugReturnList, getIdName, addDrugReturn, addDrug, editDrug, removeDrug} from '../../api/api'

    export default {
        data() {
            return {
                filters: {
                    keyword: ''
                },
                sels: [],//列表选中列
                userName: '',
                table: {
                    loading: false,
                    page: 1,
                    size: 10,
                    data: []
                },
                form: {
                    title: '',
                    visible: false,//界面是否显示
                    loading: false,
                    btnText: '提交',
                    data: {
                        id: '',
                        drugId: '',
                        drugName: '',
                        number: '',
                        sellTime: '',
                        cause: '',
                        creator: '',
                        createTime: '',
                        updateUser: '',
                        updateTime: ''
                    },
                    select: '',
                    rules: {
                        cause: [
                            {required: true, message: '请输入退货原因', trigger: 'blur'}
                        ]
                    }
                },
                pickerOptions: {
                    disabledDate(time) {
                        return time.getTime() > Date.now();
                    },
                    shortcuts: [{
                        text: '今天',
                        onClick(picker) {
                            picker.$emit('pick', new Date());
                        }
                    }, {
                        text: '昨天',
                        onClick(picker) {
                            const date = new Date();
                            date.setTime(date.getTime() - 3600 * 1000 * 24);
                            picker.$emit('pick', date);
                        }
                    }, {
                        text: '一周前',
                        onClick(picker) {
                            const date = new Date();
                            date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', date);
                        }
                    }]
                }
            };
        },
        computed: {
            drugReturnList: function () {
                var self = this, data = self.table.data, page = self.table.page, size = self.table.size,
                    keyword = self.filters.keyword;
                var v = data.filter(function (p) {
                    return (p.drugName && p.drugName.indexOf(keyword) !== -1)
                        || (p.creator && p.creator.indexOf(keyword) !== -1)
                        || (p.updateUser && p.updateUser.indexOf(keyword) !== -1)
                });
                v = v.slice((page - 1) * size, page * size);
                return v;
            }
        },
        methods: {
            getDrugReturn() {
                this.table.loading = true;
                getDrugReturnList().then((data) => {
                    this.table.data = data;
                    this.table.loading = false;
                })
            },
            getDrugIdAndName() {
                getIdName().then((data) => {
                    this.form.select = data;
                })
            },
            selsChange: function (sels) {
                this.sels = sels;
            },
            handleCurrentChange(val) {
                this.table.page = val;
            },
            handleSizeChange(val) {
                this.table.size = val;
            },
            handleAdd: function () {
                this.getDrugIdAndName();
                this.form.visible = true;
                this.form.title = '新增退货单';
                this.form.data.id = '';
                this.form.data.drugId = '';
                this.form.data.drugName = '';
                this.form.data.number = '';
                this.form.data.sellTime = '';
                this.form.data.cause = '';
                this.form.data.createTime = '';
                this.form.data.creator = this.userName;
                this.form.data.updateTime = '';
                this.form.data.updateUser = this.userName;
            },
            handleEdit: function (index, row) {
                this.form.visible = true;
                this.form.title = '编辑药品信息';
                this.form.data.id = row.id;
                this.form.data.name = row.name;
                this.form.data.manufacturers = row.manufacturers;
                this.form.data.retailPrice = row.retailPrice;
                this.form.data.dealers = row.dealers;
                this.form.data.status = row.status;
                this.form.data.number = row.number;
                this.form.data.createTime = row.createTime;
                this.form.data.creator = row.creator;
                this.form.data.updateTime = row.updateTime;
                this.form.data.updateUser = this.userName;
            },
            submit: function () {
                this.$refs.form.validate((valid) => {
                    if (valid) {
                        this.$confirm('确认提交吗?', '提示', {}).then(() => {
                            this.form.loading = true;
                            this.form.btnText = '提交中';
                            var formData = this.form.data;
                            let param = Object.assign({}, formData);
                            // if (formData.id) {
                            //     editDrug(param).then((data) => {
                            //         let {message, type} = data;
                            //         this.$message({
                            //             showClose: true,
                            //             message: message,
                            //             type: type
                            //         });
                            //         this.getDrugReturn();
                            //     });
                            // } else {
                                addDrugReturn(param).then((data) => {
                                    let {message, type} = data;
                                    this.$message({
                                        showClose: true,
                                        message: message,
                                        type: type
                                    });
                                    this.getDrugReturn();
                                });
                            // }
                            this.form.loading = false;
                            this.form.btnText = '提交';
                            this.form.visible = false;
                        });
                    }
                });
            },
            //删除
            handleDel: function (index, row) {
                this.$confirm('确认删除该记录吗?', '提示', {
                    type: 'warning'
                }).then(() => {
                    this.table.loading = true;
                    //NProgress.start();
                    let para = {id: row.id};
                    removeDrug(para).then((res) => {
                        this.table.loading = false;
                        //NProgress.done();
                        let {message, type} = res;
                        this.$message({
                            showClose: true,
                            message: message,
                            type: type
                        });
                        this.getDrugReturn();
                    });
                }).catch(() => {

                });
            },
            //获取当前用户名
            getUserName: function () {
                var user = sessionStorage.getItem('user');
                if (user) {
                    user = JSON.parse(user);
                    this.userName = user.account;
                }
            },
        },
        mounted() {
            this.getUserName();
            this.getDrugReturn();
        }
    }

</script>


