<template>
    <section>
        <!--工具条-->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true" :model="filters">
                <el-form-item>
                    <el-tooltip class="item" effect="dark" content="根据药品名称、操作人搜索" placement="right">
                        <el-input v-model="filters.keyword" placeholder="关键字"></el-input>
                    </el-tooltip>
                </el-form-item>
            </el-form>
        </el-col>

        <!--列表-->
        <template>
            <el-table :data="publishHistoryList" highlight-current-row v-loading="table.loading" style="width: 100%;">
                <el-table-column type="index" width="60">
                </el-table-column>
                <!--                <el-table-column prop="projectName" @click="publishLog" label="项目名称" sortable>-->
                <!--                    <template scope="scope">-->
                <!--                        <router-link :to="'/log/'+scope.row.id">{{ scope.row.projectName }}</router-link>-->
                <!--                    </template>-->
                <!--                </el-table-column>-->
                <el-table-column prop="drugName" label="药品名称" sortable>
                    <template scope="scope">
                        <span v-if="!statusJudge(scope.row)" style="color: #ff4949">{{scope.row.drugName}}</span>
                        <span v-else-if="statusJudge(scope.row)">{{scope.row.drugName}}</span>
                    </template>
                </el-table-column>
                <el-table-column prop="productionTime" label="生产日期" sortable>
                </el-table-column>
                <el-table-column prop="deadline" label="过期日期" sortable>
                </el-table-column>
                <el-table-column prop="price" label="入库价格" sortable>
                </el-table-column>
                <el-table-column label="操作" sortable>
                    <template scope="scope">
                        <el-tag v-if="scope.row.type === 1" type="danger">出库</el-tag>
                        <el-tag v-else-if="scope.row.type === 0" type="success">入库</el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="状态" sortable>
                    <template scope="scope">
                        <el-tag v-if="statusJudge(scope.row)" type="success">正常</el-tag>
                        <el-tag v-else-if="!statusJudge(scope.row)" type="danger">临期</el-tag>
                    </template>
                </el-table-column>
                <el-table-column prop="number" label="出入库数量" sortable>
                </el-table-column>
                <el-table-column prop="creator" label="操作人" sortable>
                </el-table-column>
                <el-table-column prop="createTime" label="出入库时间" sortable>
                </el-table-column>
                <el-table-column prop="statement" label="说明" sortable>
                </el-table-column>
            </el-table>

            <!--工具条-->
            <el-col :span="24" class="toolbar" style="padding-bottom:10px;">
                <!--分页-->
                <el-pagination
                        @current-change="handleCurrentChange"
                        @size-change="handleSizeChange"
                        :current-page="table.page" :page-sizes="[10, 25, 50]" :page-size="table.size"
                        layout="total, sizes, prev, pager, next, jumper"
                        :total="table.data.length" style="float:right">
                </el-pagination>
            </el-col>
        </template>

    </section>
</template>
<script>
    import {getInventoryHistory} from '../../api/api';

    export default {
        data() {
            return {
                filters: {
                    keyword: ''
                },
                table: {
                    loading: false,
                    page: 1,
                    size: 10,
                    data: []
                }
            }
        },
        computed: {
            publishHistoryList: function () {
                var self = this, data = self.table.data, page = self.table.page, size = self.table.size,
                    keyword = self.filters.keyword;
                var v = data.filter(function (p) {
                    return (p.drugName && p.drugName.indexOf(keyword) !== -1)
                        || (p.creator && p.creator.indexOf(keyword) !== -1)
                });
                v = v.slice((page - 1) * size, page * size);
                return v;
            }
        },
        methods: {
            statusJudge: function (row) {
                if (row.productionTime === undefined) {
                    return true;
                }
                let start = new Date(row.productionTime);
                let end = new Date(row.deadline);
                let day = (end - start) / (1 * 24 * 60 * 60 * 1000);
                return day >= 30;
            },
            getInventoryHistory(param) {
                this.table.loading = true;
                getInventoryHistory(param).then((data) => {
                    this.table.data = data;
                    this.table.loading = false;
                })
            },
            handleCurrentChange(val) {
                this.table.page = val;
            },
            handleSizeChange(val) {
                this.table.size = val;
            }
        },
        mounted() {
            const drugId = this.$route.query.id
            if (drugId === undefined) {
                this.getInventoryHistory(0);
            } else {
                this.getInventoryHistory(drugId)
            }
        }
    };

</script>

<style scoped>

</style>


