<template>
    <section>
        <!--工具条-->
        <el-row :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-col :span="12">
                <el-form :inline="true" :model="filters" class="grid-content bg-purple">
                    <el-form-item>
                        <el-tooltip class="item" effect="dark" content="根据药品名称、生产厂商搜索" placement="right">
                            <el-input v-model="filters.keyword" placeholder="关键字"></el-input>
                        </el-tooltip>
                    </el-form-item>
                </el-form>
            </el-col>
        </el-row>

        <!--列表-->
        <el-table :data="drugList" show-summary border highlight-current-row v-loading="table.loading"
                  element-loading-text="拼命加载中"
                  @selection-change="selsChange" style="width: 100%;">
            <el-table-column type="selection" width="60">
            </el-table-column>
            <el-table-column type="index" width="60">
            </el-table-column>
            <el-table-column prop="name" label="药品名称" sortable>
                <!--                <template scope="scope">-->
                <!--                    <router-link :to="{ path: '/inventory-history',query:{id:scope.row.id} }">{{ scope.row.name }}-->
                <!--                    </router-link>-->
                <!--                </template>-->
            </el-table-column>
            <el-table-column prop="manufacturers" label="生产厂商" sortable>
            </el-table-column>
            <el-table-column prop="retailPrice" label="零售价" sortable>
            </el-table-column>
            <el-table-column prop="number" label="数量" sortable>
                <template scope="scope">
                    <el-tag v-if="scope.row.number < 5" type="danger">{{scope.row.number}}</el-tag>
                    <el-tag v-else-if="scope.row.number >= 5" type="success">{{scope.row.number}}</el-tag>
                </template>
            </el-table-column>
            <el-table-column prop="sellNumber" label="出售数量" sortable>
                <template scope="scope">
                    <el-input-number size="small" v-model="scope.row.sellNumber"
                                     @change="((curr)=>{numberChange(curr,scope.row)})"
                                     controls-position="right" :max="scope.row.number" :min="0"></el-input-number>
                </template>
            </el-table-column>
            <el-table-column prop="totalPrice" label="总计" sortable>
            </el-table-column>
            <!--            <el-table-column label="操作" min-width="120">-->
            <!--                <template scope="scope">-->
            <!--                    <el-button type="info" size="small" @click="change(scope.$index, scope.row)">修改零售价</el-button>-->
            <!--                    <el-button type="danger" size="small" @click="sell(scope.$index, scope.row)"-->
            <!--                               :disabled="scope.row.number<=0">出售-->
            <!--                    </el-button>-->
            <!--                </template>-->
            <!--            </el-table-column>-->
        </el-table>

        <!--工具条-->
        <el-col :span="24" class="toolbar" style="padding-bottom:10px;">
            <el-button type="info" @click="submit" :disabled="this.sels.length===0">批量出售
            </el-button>
            <!--分页-->
            <el-pagination
                    @current-change="handleCurrentChange"
                    @size-change="handleSizeChange"
                    :current-page="table.page" :page-sizes="[10, 25, 50]" :page-size="table.size"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="drugList.length" style="float:right">
            </el-pagination>
        </el-col>

        <!--出售-->
        <!--        <el-dialog :title="form.title" v-model="form.delVisible" :close-on-click-modal="false">-->
        <!--            <el-form :model="form.data " label-width="100px" :rules="form.rules" ref="form">-->
        <!--                <el-form-item label="药品名称" prop="drugName">-->
        <!--                    <el-input v-model="form.data.drugName" auto-complete="off" :disabled="true"></el-input>-->
        <!--                </el-form-item>-->
        <!--                <el-form-item label="单价" prop="retailPrice">-->
        <!--                    <el-input v-model="form.data.retailPrice" auto-complete="off" :disabled="true"></el-input>-->
        <!--                </el-form-item>-->
        <!--                <el-form-item label="总计" prop="totalPrice">-->
        <!--                    <el-input v-model="form.data.totalPrice" auto-complete="off" :disabled="true"></el-input>-->
        <!--                </el-form-item>-->
        <!--                <el-form-item label="数量" prop="number">-->
        <!--                    <el-input-number v-model="form.data.number" @change="numberChange" :max="form.maxNumber"-->
        <!--                                     :min="1"></el-input-number>-->
        <!--                </el-form-item>-->
        <!--                <el-form-item label="说明" prop="statement">-->
        <!--                    <el-input type="textarea" v-model="form.data.statement" placeholder="说明"></el-input>-->
        <!--                </el-form-item>-->
        <!--            </el-form>-->
        <!--            <div slot="footer" class="dialog-footer">-->
        <!--                <el-button @click.native="form.delVisible = false">取 消</el-button>-->
        <!--                <el-button type="primary" @click.native="submit" :loading="form.loading">-->
        <!--                    <div>{{form.btnText}}</div>-->
        <!--                </el-button>-->
        <!--            </div>-->
        <!--        </el-dialog>-->

    </section>
</template>

<script>
    import {getDrugList, editRetailPrice, batchAddSellHistory} from '../../api/api'

    export default {
        data() {
            return {
                filters: {
                    keyword: ''
                },
                sels: [],//列表选中列
                userName: '',
                table: {
                    loading: false,
                    page: 1,
                    size: 10,
                    data: []
                },
                form: {
                    title: '',
                    addVisible: false,//修改界面是否显示
                    delVisible: false,//出售界面是否显示
                    maxNumber: '',//出售最大值
                    loading: false,
                    btnText: '提交',
                    data: {
                        id: '',
                        drugId: '',
                        drugName: '',
                        retailPrice: '',
                        number: '',
                        totalPrice: '',
                        statement: '',
                        creator: '',
                        createTime: '',
                    },
                    changeData: {
                        drugId: '',
                        retailPrice: '',
                        drugName: ''
                    },
                    range: '',
                    rules: {}
                }
            }
        },
        computed: {
            drugList: function () {
                var self = this, data = self.table.data, page = self.table.page, size = self.table.size,
                    keyword = self.filters.keyword;
                var v = data.filter(function (p) {
                    return (p.name && p.name.indexOf(keyword) !== -1)
                        || (p.manufacturers && p.manufacturers.indexOf(keyword) !== -1)
                        || (p.creator && p.creator.indexOf(keyword) !== -1)
                        || (p.updateUser && p.updateUser.indexOf(keyword) !== -1)
                });
                v = v.slice((page - 1) * size, page * size);
                return v;
            }
        },
        methods: {
            numberChange: function (curr, row) {
                row.creator = this.userName;
                row.totalPrice = parseFloat(curr * 10000 * row.retailPrice / 10000);
            },
            getDrug() {
                this.table.loading = true;
                getDrugList().then((data) => {
                    this.table.data = data;
                    this.table.data.forEach(v => {
                        // v.sellNumber = 0;
                        v.totalPrice = 0;
                    });
                    this.table.loading = false;
                })
            },
            selsChange: function (sels) {
                this.sels = sels;
            },
            handleCurrentChange(val) {
                this.table.page = val;
            },
            handleSizeChange(val) {
                this.table.size = val;
            },
            // change: function (index, row) {
            //     this.form.addVisible = true;
            //     this.form.title = '修改零售价格';
            //     this.form.changeData.drugId = row.id;
            //     this.form.changeData.drugName = row.name;
            //     this.form.changeData.retailPrice = row.retailPrice;
            // },
            // sell: function (index, row) {
            //     this.form.maxNumber = row.number;
            //     this.form.delVisible = true;
            //     this.form.title = '出售';
            //     this.form.data.id = '';
            //     this.form.data.drugId = row.id;
            //     this.form.data.drugName = row.name;
            //     this.form.data.retailPrice = row.retailPrice;
            //     this.form.data.number = '';
            //     this.form.data.totalPrice = row.retailPrice;
            //     this.form.data.statement = '';
            //     this.form.data.creator = this.userName;
            //     this.form.data.createTime = '';
            // },
            submit: function () {
                let flag = false;
                this.sels.forEach(row => {
                    if (row.sellNumber < 1) {
                        this.$message({
                            message: "出售数量不能为0",
                            type: 'error'
                        });
                        flag = true;
                    }
                });
                if (flag) {
                    return;
                }
                this.$confirm('确认提交吗?', '提示', {}).then(() => {
                    this.form.loading = true;
                    this.form.btnText = '提交中';
                    let formData = this.sels;
                    let param = Object.assign({}, formData);
                    batchAddSellHistory(formData).then((data) => {
                        let {message, type} = data;
                        this.$message({
                            showClose: true,
                            message: message,
                            type: type
                        });
                        this.getDrug();
                    });
                    this.form.loading = false;
                    this.form.btnText = '提交';
                    this.form.delVisible = false;
                });
            },
            changeSubmit: function () {
                this.$refs.form.validate((valid) => {
                    if (valid) {
                        this.$confirm('确认提交吗?', '提示', {}).then(() => {
                            this.form.loading = true;
                            this.form.btnText = '提交中';
                            var formData = this.form.changeData;
                            let param = Object.assign({}, formData);
                            editRetailPrice(param).then((data) => {
                                let {message, type} = data;
                                this.$message({
                                    showClose: true,
                                    message: message,
                                    type: type
                                });
                                this.getDrug();
                            });
                            this.form.loading = false;
                            this.form.btnText = '提交';
                            this.form.addVisible = false;
                        });
                    }
                });
            },
            //获取当前用户名
            getUserName: function () {
                var user = sessionStorage.getItem('user');
                if (user) {
                    user = JSON.parse(user);
                    this.userName = user.account;
                }
            }
        },
        mounted() {
            this.getDrug();
            this.getUserName();
        }
    }

</script>


