<template>
    <section>
        <!--工具条-->
        <el-row :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-col :span="12">
                <el-form :inline="true" :model="filters" class="grid-content bg-purple">
                    <el-form-item>
                        <el-tooltip class="item" effect="dark" content="根据名称、生产厂商、经销商搜索" placement="right">
                            <el-input v-model="filters.keyword" placeholder="关键字"></el-input>
                        </el-tooltip>
                    </el-form-item>
                </el-form>
            </el-col>
            <el-col :span="1" :offset="10">
                <el-form class="grid-content bg-purple-light">
                    <el-form-item>
                        <el-button type="primary" @click="handleAdd" v-if="this.userName==='admin'">添加药品</el-button>
                    </el-form-item>
                </el-form>
            </el-col>
        </el-row>

        <!--列表-->
        <el-table :data="drugList" highlight-current-row v-loading="table.loading" element-loading-text="拼命加载中"
                  @selection-change="selsChange" style="width: 100%;">
            <!--            <el-table-column type="selection" width="55">-->
            <!--            </el-table-column>-->
            <el-table-column type="index" width="60">
            </el-table-column>
            <el-table-column prop="name" label="药品名称" sortable>
            </el-table-column>
            <el-table-column prop="manufacturers" label="生产厂商" sortable>
            </el-table-column>
            <!--            <el-table-column prop="productionTime" label="生产日期" sortable>-->
            <!--            </el-table-column>-->
            <!--            <el-table-column prop="deadline" label="过期日期" sortable>-->
            <!--            </el-table-column>-->
            <el-table-column prop="dealers" label="经销商" sortable>
            </el-table-column>
            <el-table-column prop="creator" label="创建人" sortable>
            </el-table-column>
            <el-table-column prop="createTime" label="创建时间" sortable>
            </el-table-column>
            <el-table-column prop="updateUser" label="修改人" sortable>
            </el-table-column>
            <el-table-column prop="updateTime" label="修改时间" sortable>
            </el-table-column>
            <el-table-column label="操作" min-width="120">
                <template scope="scope">
                    <el-button size="small" @click="handleEdit(scope.$index, scope.row)" v-if="userName==='admin'">
                        编辑
                    </el-button>
                    <el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"
                               v-if="userName==='admin'">删除
                    </el-button>
                </template>
            </el-table-column>
        </el-table>

        <!--工具条-->
        <el-col :span="24" class="toolbar" style="padding-bottom:10px;">
            <!--分页-->
            <el-pagination
                    @current-change="handleCurrentChange"
                    @size-change="handleSizeChange"
                    :current-page="table.page" :page-sizes="[10, 25, 50]" :page-size="table.size"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="drugList.length" style="float:right">
            </el-pagination>
        </el-col>

        <!--新增、编辑界面-->
        <el-dialog :title="form.title" v-model="form.visible" :close-on-click-modal="false">
            <el-form :model="form.data " label-width="100px" :rules="form.rules" ref="form">
                <el-form-item label="药品名称" prop="name">
                    <el-input v-model="form.data.name" auto-complete="off" placeholder="药品名称"></el-input>
                </el-form-item>
                <el-form-item label="生产厂商" prop="manufacturers">
                    <el-input v-model="form.data.manufacturers" auto-complete="off" placeholder="生产厂商"></el-input>
                </el-form-item>
                <el-form-item label="经销商" prop="dealers">
                    <el-input v-model="form.data.dealers" placeholder="经销商"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="form.visible = false">取 消</el-button>
                <!--<el-button type="danger" @click="handleDel(form.data)" v-show="form.data.id != ''">删除</el-button>-->
                <el-button type="primary" @click.native="submit" :loading="form.loading">
                    <div>{{form.btnText}}</div>
                </el-button>
            </div>
        </el-dialog>

    </section>
</template>

<script>
    import {getDrugList, addDrug, editDrug, removeDrug} from '../../api/api'

    export default {
        data() {
            return {
                filters: {
                    keyword: ''
                },
                sels: [],//列表选中列
                table: {
                    loading: false,
                    page: 1,
                    size: 10,
                    data: []
                },
                form: {
                    title: '',
                    visible: false,//界面是否显示
                    loading: false,
                    btnText: '提交',
                    data: {
                        id: '',
                        name: '',
                        manufacturers: '',
                        retailPrice: '',
                        dealers: '',
                        status: '',
                        number: '',
                        creator: '',
                        createTime: '',
                        updateUser: '',
                        updateTime: ''
                    },
                    rules: {
                        name: [
                            {required: true, message: '请输入药品名称', trigger: 'blur'}
                        ],
                        manufacturers: [
                            {required: true, message: '请输入生产厂商', trigger: 'blur'}
                        ],
                        dealers: [
                            {required: true, message: '请输入经销商', trigger: 'blur'}
                        ]
                    }
                },
                userName: ''
            };
        },
        computed: {
            drugList: function () {
                var self = this, data = self.table.data, page = self.table.page, size = self.table.size,
                    keyword = self.filters.keyword;
                var v = data.filter(function (p) {
                    return (p.name && p.name.indexOf(keyword) !== -1)
                        || (p.manufacturers && p.manufacturers.indexOf(keyword) !== -1)
                        || (p.dealers && p.dealers.indexOf(keyword) !== -1)
                        || (p.creator && p.creator.indexOf(keyword) !== -1)
                        || (p.updateUser && p.updateUser.indexOf(keyword) !== -1)
                });
                v = v.slice((page - 1) * size, page * size);
                return v;
            }
        },
        methods: {
            getDrug() {
                this.table.loading = true;
                getDrugList().then((data) => {
                    this.table.data = data;
                    this.table.loading = false;
                })
            },
            selsChange: function (sels) {
                this.sels = sels;
            },
            handleCurrentChange(val) {
                this.table.page = val;
            },
            handleSizeChange(val) {
                this.table.size = val;
            },
            handleAdd: function () {
                this.form.visible = true;
                this.form.title = '新增药品';
                this.form.data.id = '';
                this.form.data.name = '';
                this.form.data.manufacturers = '';
                this.form.data.retailPrice = '';
                this.form.data.dealers = '';
                this.form.data.status = '';
                this.form.data.number = '';
                this.form.data.createTime = '';
                this.form.data.creator = this.userName;
                this.form.data.updateTime = '';
                this.form.data.updateUser = this.userName;
            },
            handleEdit: function (index, row) {
                this.form.visible = true;
                this.form.title = '编辑药品信息';
                this.form.data.id = row.id;
                this.form.data.name = row.name;
                this.form.data.manufacturers = row.manufacturers;
                this.form.data.retailPrice = row.retailPrice;
                this.form.data.dealers = row.dealers;
                this.form.data.status = row.status;
                this.form.data.number = row.number;
                this.form.data.createTime = row.createTime;
                this.form.data.creator = row.creator;
                this.form.data.updateTime = row.updateTime;
                this.form.data.updateUser = this.userName;
            },
            submit: function () {
                this.$refs.form.validate((valid) => {
                    if (valid) {
                        this.$confirm('确认提交吗?', '提示', {}).then(() => {
                            this.form.loading = true;
                            this.form.btnText = '提交中';
                            var formData = this.form.data;
                            let param = Object.assign({}, formData);
                            if (formData.id) {
                                editDrug(param).then((data) => {
                                    let {message, type} = data;
                                    this.$message({
                                        showClose: true,
                                        message: message,
                                        type: type
                                    });
                                    this.getDrug();
                                });
                            } else {
                                addDrug(param).then((data) => {
                                    let {message, type} = data;
                                    this.$message({
                                        showClose: true,
                                        message: message,
                                        type: type
                                    });
                                    this.getDrug();
                                });
                            }
                            this.form.loading = false;
                            this.form.btnText = '提交';
                            this.form.visible = false;
                        });
                    }
                });
            },
            //删除
            handleDel: function (index, row) {
                this.$confirm('确认删除该记录吗?', '提示', {
                    type: 'warning'
                }).then(() => {
                    this.table.loading = true;
                    //NProgress.start();
                    let para = {id: row.id};
                    removeDrug(para).then((res) => {
                        this.table.loading = false;
                        //NProgress.done();
                        let {message, type} = res;
                        this.$message({
                            showClose: true,
                            message: message,
                            type: type
                        });
                        this.getDrug();
                    });
                }).catch(() => {

                });
            },
            //获取当前用户名
            getUserName: function () {
                var user = sessionStorage.getItem('user');
                if (user) {
                    user = JSON.parse(user);
                    this.userName = user.account;
                }
            },
        },
        mounted() {
            this.getUserName();
            console.log(this.userName);
            this.getDrug();
        }
    }

</script>


