<template>
    <section>
        <!--工具条-->
        <el-row :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-col :span="12">
                <el-form :inline="true" :model="filters" class="grid-content bg-purple">
                    <el-form-item>
                        <el-tooltip class="item" effect="dark" content="根据标题搜索" placement="right">
                            <el-input v-model="filters.keyword" placeholder="关键字"></el-input>
                        </el-tooltip>
                    </el-form-item>
                </el-form>
            </el-col>
            <el-col :span="1" :offset="10">
                <el-form class="grid-content bg-purple-light">
                    <el-form-item>
                        <el-button type="primary" @click="handleAdd">添加投诉</el-button>
                    </el-form-item>
                </el-form>
            </el-col>
        </el-row>

        <!--列表-->
        <el-table :data="feedbackList" highlight-current-row v-loading="table.loading" element-loading-text="拼命加载中"
                  @selection-change="selsChange" style="width: 100%;">
            <el-table-column type="index" width="60">
            </el-table-column>
            <el-table-column prop="title" label="标题" sortable>
            </el-table-column>
            <el-table-column prop="type" label="类型" sortable>
                <template scope="scope">
                    <el-tag v-if="scope.row.type === 0" type="danger">投诉</el-tag>
                    <el-tag v-else-if="scope.row.type === 1" type="success">反馈</el-tag>
                </template>
            </el-table-column>
            <el-table-column prop="status" label="状态" sortable>
                <template scope="scope">
                    <el-tag v-if="scope.row.status === 0" type="primary">新建</el-tag>
                    <el-tag v-else-if="scope.row.status === 1" type="info">跟进中</el-tag>
                    <el-tag v-else-if="scope.row.status === 2" type="success">完成</el-tag>
                </template>
            </el-table-column>
            <el-table-column prop="statement" label="说明" sortable>
            </el-table-column>
            <el-table-column prop="creator" label="创建人" sortable>
            </el-table-column>
            <el-table-column prop="createTime" label="创建时间" sortable>
            </el-table-column>
            <!--            <el-table-column prop="updateUser" label="修改人" sortable>-->
            <!--            </el-table-column>-->
            <!--            <el-table-column prop="updateTime" label="修改时间" sortable>-->
            <!--            </el-table-column>-->
            <el-table-column label="操作" min-width="120">
                <template scope="scope">
                    <el-button type="info" size="small" @click="handleInfo(scope.$index, scope.row)">详情</el-button>
                    <el-button type="success" size="small" @click="handleChangeStatus(scope.row.id,1)"
                               v-if="scope.row.status === 0">跟进
                    </el-button>
                    <el-button type="success" size="small" @click="handleChangeStatus(scope.row.id,2)"
                               v-else-if="scope.row.status === 1">完成
                    </el-button>
                    <el-button size="small" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                </template>
            </el-table-column>
        </el-table>

        <!--工具条-->
        <el-col :span="24" class="toolbar" style="padding-bottom:10px;">
            <!--分页-->
            <el-pagination
                    @current-change="handleCurrentChange"
                    @size-change="handleSizeChange"
                    :current-page="table.page" :page-sizes="[10, 25, 50]" :page-size="table.size"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="feedbackList.length" style="float:right">
            </el-pagination>
        </el-col>

        <!--新增、编辑界面-->
        <el-dialog :title="form.title" v-model="form.visible" :close-on-click-modal="false">
            <el-form :model="form.data " label-width="100px" :rules="form.rules" ref="form">
                <el-form-item label="标题" prop="title">
                    <el-input v-model="form.data.title" auto-complete="off" placeholder="标题"></el-input>
                </el-form-item>
                <el-form-item label="内容" prop="info">
                    <el-input type="textarea" :rows="8" v-model="form.data.info" auto-complete="off"
                              placeholder="内容"></el-input>
                </el-form-item>
                <el-form-item label="类型" prop="type">
                    <el-tag type="danger">投诉</el-tag>
                </el-form-item>
                <el-form-item label="说明" prop="statement">
                    <el-input type="textarea" v-model="form.data.statement" auto-complete="off"
                              placeholder="说明"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="form.visible = false">取 消</el-button>
                <!--<el-button type="danger" @click="handleDel(form.data)" v-show="form.data.id != ''">删除</el-button>-->
                <el-button type="primary" @click.native="submit" :loading="form.loading">
                    <div>{{form.btnText}}</div>
                </el-button>
            </div>
        </el-dialog>

        <el-dialog :title="form.info.text" v-model="form.info.visible" :close-on-click-modal="false">
            <el-form :model="form.info " label-width="100px" :rules="form.rules" ref="form">
                <el-form-item label="标题" prop="title">
                    <el-input v-model="form.info.title" auto-complete="off" :disabled="true"
                              placeholder="标题"></el-input>
                </el-form-item>
                <el-form-item label="内容" prop="info">
                    <el-input type="textarea" :rows="8" :disabled="true" v-model="form.info.info" auto-complete="off"
                              placeholder="内容"></el-input>
                </el-form-item>
                <!--                <el-form-item label="类型" prop="type">-->
                <!--                    <el-tag type="danger">投诉</el-tag>-->
                <!--                </el-form-item>-->
                <!--                <el-form-item label="说明" prop="statement">-->
                <!--                    <el-input type="textarea" v-model="form.data.statement" auto-complete="off"-->
                <!--                              placeholder="说明"></el-input>-->
                <!--                </el-form-item>-->
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="form.info.visible = false">取 消</el-button>
                <!--<el-button type="danger" @click="handleDel(form.data)" v-show="form.data.id != ''">删除</el-button>-->
                <!--                <el-button type="primary" @click.native="submit" :loading="form.loading">-->
                <!--                    <div>{{form.btnText}}</div>-->
                <!--                </el-button>-->
            </div>
        </el-dialog>

    </section>
</template>

<script>
    import {getFeedback, addFeedback, editFeedback, editStatus} from '../../api/api'

    export default {
        data() {
            return {
                filters: {
                    keyword: ''
                },
                sels: [],//列表选中列
                userName: '',
                table: {
                    loading: false,
                    text: '',
                    page: 1,
                    size: 10,
                    data: []
                },
                form: {
                    title: '',
                    visible: false,//界面是否显示
                    info: {
                        visible: false,
                        title: '',
                        info: ''
                    },
                    loading: false,
                    btnText: '提交',
                    data: {
                        id: '',
                        title: '',
                        info: '',
                        type: '',
                        status: '',
                        statement: '',
                        creator: '',
                        createTime: '',
                        updateUser: '',
                        updateTime: ''
                    },
                    rules: {
                        title: [
                            {required: true, message: '请输入标题', trigger: 'blur'}
                        ],
                        info: [
                            {required: true, message: '请输入内容', trigger: 'blur'}
                        ]
                    }
                }
            };
        },
        computed: {
            feedbackList: function () {
                var self = this, data = self.table.data, page = self.table.page, size = self.table.size,
                    keyword = self.filters.keyword;
                var v = data.filter(function (p) {
                    return (p.title && p.title.indexOf(keyword) !== -1)
                        || (p.creator && p.creator.indexOf(keyword) !== -1)
                        || (p.updateUser && p.updateUser.indexOf(keyword) !== -1)
                });
                v = v.slice((page - 1) * size, page * size);
                return v;
            }
        },
        methods: {
            getFeedback() {
                this.table.loading = true;
                getFeedback(0).then((data) => {
                    this.table.data = data;
                    this.table.loading = false;
                })
            },
            selsChange: function (sels) {
                this.sels = sels;
            },
            handleCurrentChange(val) {
                this.table.page = val;
            },
            handleSizeChange(val) {
                this.table.size = val;
            },
            handleAdd: function () {
                this.form.visible = true;
                this.form.title = '添加';
                this.form.data.id = '';
                this.form.data.title = '';
                this.form.data.info = '';
                this.form.data.type = 0;
                this.form.data.status = 0;
                this.form.data.statement = '';
                this.form.data.createTime = '';
                this.form.data.creator = this.userName;
                this.form.data.updateTime = '';
                this.form.data.updateUser = this.userName;
            },
            handleEdit: function (index, row) {
                this.form.visible = true;
                this.form.title = '编辑';
                this.form.data.id = row.id;
                this.form.data.title = row.title;
                this.form.data.info = row.info;
                this.form.data.type = row.type;
                this.form.data.status = row.status;
                this.form.data.statement = row.statement;
                this.form.data.number = row.number;
                this.form.data.createTime = row.createTime;
                this.form.data.creator = row.creator;
                this.form.data.updateTime = row.updateTime;
                this.form.data.updateUser = this.userName;
            },
            handleChangeStatus: function (id, status) {
                this.$confirm('确认提交吗?', '提示', {}).then(() => {
                    let param = {"feedbackId": id, "status": status,}
                    editStatus(param).then((data) => {
                        let {message, type} = data;
                        this.$message({
                            showClose: true,
                            message: message,
                            type: type
                        });
                        this.getFeedback();
                    });
                });
            },
            submit: function () {
                this.$refs.form.validate((valid) => {
                    if (valid) {
                        this.$confirm('确认提交吗?', '提示', {}).then(() => {
                            this.form.loading = true;
                            this.form.btnText = '提交中';
                            var formData = this.form.data;
                            let param = Object.assign({}, formData);
                            if (formData.id) {
                                editFeedback(param).then((data) => {
                                    let {message, type} = data;
                                    this.$message({
                                        showClose: true,
                                        message: message,
                                        type: type
                                    });
                                    this.getFeedback();
                                });
                            } else {
                                addFeedback(param).then((data) => {
                                    let {message, type} = data;
                                    this.$message({
                                        showClose: true,
                                        message: message,
                                        type: type
                                    });
                                    this.getFeedback();
                                });
                            }
                            this.form.loading = false;
                            this.form.btnText = '提交';
                            this.form.visible = false;
                        });
                    }
                });
            },
            //详情
            handleInfo: function (index, row) {
                this.form.info.visible = true;
                this.form.info.text = "详情";
                this.form.info.title = row.title;
                this.form.info.info = row.info;
            },
            //获取当前用户名
            getUserName: function () {
                var user = sessionStorage.getItem('user');
                if (user) {
                    user = JSON.parse(user);
                    this.userName = user.account;
                }
            },
        },
        mounted() {
            this.getUserName();
            this.getFeedback();
        }
    }

</script>


