import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Main from './views/Main.vue'
import User from './views/user/User.vue'
import Drug from './views/drug/Drug.vue'
import DrugInventory from './views/drug/DrugInventory.vue'
import inventoryHistory from './views/drug/InventoryHistory.vue'
import SellManage from './views/sellManage/SellManage.vue'
import SellHistory from './views/sellManage/SellHistory.vue'
import Complain from './views/feedback/Complain.vue'
import Feedback from './views/feedback/Feedback.vue'
import DrugShow from './views/drugShow/DrugShow.vue'
import echarts from './views/statistics/echarts.vue'
import DrugReturn from "./views/DrugReturn/DrugReturn.vue";
import BatchSellManage from "./views/sellManage/BatchSellManage.vue";

let routes = [
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    //{ path: '/main', component: Main },
    {
        path: '/',
        component: Home,
        name: '药品管理',
        iconCls: 'el-icon-message',//图标样式class
        children: [
            {path: '/drug', component: Drug, name: '药品信息'},
            {path: '/drug-inventory', component: DrugInventory, name: '库存管理'},
            {path: '/inventory-history', component: inventoryHistory, name: '出入库历史'},
            // {path: '/drug-history/:drugId', component: PublishLog, name: '发布详情', hidden: true},
        ]
    },
    {
        path: '/',
        component: Home,
        name: '柜台管理',
        iconCls: 'el-icon-message',//图标样式class
        leaf: true,
        children: [
            {path: '/drug-show', component: DrugShow, name: '柜台管理'},
        ]
    },
    {
        path: '/',
        component: Home,
        name: '销售管理',
        iconCls: 'fa fa-id-card-o',
        children: [
            {path: '/sell', component: SellManage, name: '销售管理'},
            {path: '/sell-batch', component: BatchSellManage, name: '批量出售', hidden: true},
            {path: '/sell-history', component: SellHistory, name: '销售历史'},
        ]
    },
    {
        path: '/',
        component: Home,
        name: '退货管理',
        iconCls: 'fa fa-id-card-o',
        leaf: true,
        children: [
            {path: '/drug-return', component: DrugReturn, name: '退货管理'}
        ]
    },
    {
        path: '/',
        component: Home,
        name: '投诉&反馈',
        iconCls: 'fa fa-id-card-o',
        children: [
            {path: '/complain', component: Complain, name: '投诉'},
            {path: '/feedback', component: Feedback, name: '反馈'},
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-address-card',
        leaf: true,//只有一个节点
        children: [
            {path: '/user', component: User, name: '用户管理'}
        ]
    },
    {
        path: '*',
        hidden: true,
        redirect: {path: '/404'}
    }
];

// let user = JSON.parse(sessionStorage.getItem('user'));
// if (user && user.account === 'admin') {
//     routes.push()
// }

export default routes;


