<template>
    <section>
        <!--工具条-->
        <el-row :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-col :span="12">
                <el-form :inline="true" :model="filters" class="grid-content bg-purple">
                    <el-form-item>
                        <el-tooltip class="item" effect="dark" content="根据名称、生产厂商搜索" placement="right">
                            <el-input v-model="filters.keyword" placeholder="关键字"></el-input>
                        </el-tooltip>
                    </el-form-item>
                </el-form>
            </el-col>
        </el-row>

        <!--列表-->
        <el-table :data="drugList" highlight-current-row v-loading="table.loading" element-loading-text="拼命加载中"
                  @selection-change="selsChange" style="width: 100%;">
            <!--            <el-table-column type="selection" width="55">-->
            <!--            </el-table-column>-->
            <el-table-column type="index" width="60">
            </el-table-column>
            <el-table-column prop="name" label="药品名称" sortable>
                <template scope="scope">
                    <router-link :to="{ path: '/inventory-history',query:{id:scope.row.id} }">{{ scope.row.name }}
                    </router-link>
                </template>
            </el-table-column>
            <el-table-column prop="manufacturers" label="生产厂商" sortable>
            </el-table-column>
            <!--            <el-table-column prop="productionTime" label="生产日期" sortable>-->
            <!--            </el-table-column>-->
            <!--            <el-table-column prop="deadline" label="过期日期" sortable>-->
            <!--            </el-table-column>-->
            <!--            <el-table-column prop="dealers" label="经销商" sortable>-->
            <!--            </el-table-column>-->
            <el-table-column prop="creator" label="创建人" sortable>
            </el-table-column>
            <el-table-column prop="createTime" label="创建时间" sortable>
            </el-table-column>
            <el-table-column prop="updateUser" label="修改人" sortable>
            </el-table-column>
            <el-table-column prop="updateTime" label="修改时间" sortable>
            </el-table-column>
            <el-table-column prop="number" label="数量" sortable>
                <template scope="scope">
                    <el-tag v-if="scope.row.number < 5" type="danger">{{scope.row.number}}</el-tag>
                    <el-tag v-else-if="scope.row.number >=5" type="success">{{scope.row.number}}</el-tag>
                </template>
            </el-table-column>
            <el-table-column label="操作" min-width="120">
                <template scope="scope">
                    <el-button type="info" size="small" v-if="userName==='admin'" @click="add(scope.$index, scope.row)">入库</el-button>
                    <el-button type="danger" size="small" v-if="userName==='admin'" @click="del(scope.$index, scope.row)">出库</el-button>
                </template>
            </el-table-column>
        </el-table>

        <!--工具条-->
        <el-col :span="24" class="toolbar" style="padding-bottom:10px;">
            <!--分页-->
            <el-pagination
                    @current-change="handleCurrentChange"
                    @size-change="handleSizeChange"
                    :current-page="table.page" :page-sizes="[10, 25, 50]" :page-size="table.size"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="drugList.length" style="float:right">
            </el-pagination>
        </el-col>

        <!--入库-->
        <el-dialog :title="form.title" v-model="form.addVisible" :close-on-click-modal="false">
            <el-form :model="form.data " label-width="100px" :rules="form.rules" ref="form">
                <el-form-item label="药品名称" prop="drugName">
                    <el-input v-model="form.data.drugName" auto-complete="off" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="有效期">
                    <div class="block">
                        <el-date-picker
                                v-model="form.range"
                                type="daterange"
                                unlink-panels
                                @change="handleChange"
                                range-separator="至"
                                start-placeholder="开始日期"
                                end-placeholder="结束日期">
                        </el-date-picker>
                    </div>
                </el-form-item>
                <el-form-item label="入库价格" prop="price">
                    <el-input-number v-model="form.data.price" :precision="4" :step="0.1" :min="0"></el-input-number>
                </el-form-item>
                <el-form-item label="入库数量" prop="number">
                    <el-input-number v-model="form.data.number" :min="1" label="描述文字"></el-input-number>
                </el-form-item>
                <el-form-item label="说明" prop="statement">
                    <el-input type="textarea" v-model="form.data.statement" placeholder="说明"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="form.addVisible = false">取 消</el-button>
                <el-button type="primary" @click.native="submit" :loading="form.loading">
                    <div>{{form.btnText}}</div>
                </el-button>
            </div>
        </el-dialog>

        <!--出库-->
        <el-dialog :title="form.title" v-model="form.delVisible" :close-on-click-modal="false">
            <el-form :model="form.data " label-width="100px" :rules="form.rules" ref="form">
                <el-form-item label="药品名称" prop="drugName">
                    <el-input v-model="form.data.drugName" auto-complete="off" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="出库数量" prop="number">
                    <el-input-number v-model="form.data.number" :max="form.maxNumber" :min="1"
                                     label="描述文字"></el-input-number>
                </el-form-item>
                <el-form-item label="说明" prop="statement">
                    <!--                    <el-input type="textarea" v-model="form.data.statement" placeholder="说明"></el-input>-->
                    <template>
                        <el-select v-model="form.data.statement" filterable placeholder="请选择">
                            <el-option
                                    v-for="item in statementSelect"
                                    :key="item"
                                    :label="item"
                                    :value="item">
                            </el-option>
                        </el-select>
                    </template>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="form.delVisible = false">取 消</el-button>
                <el-button type="primary" @click.native="submit" :loading="form.loading">
                    <div>{{form.btnText}}</div>
                </el-button>
            </div>
        </el-dialog>

    </section>
</template>

<script>
    import {getDrugList, addDrugInventory, removeDrugInventory} from '../../api/api'

    export default {
        data() {
            return {
                filters: {
                    keyword: ''
                },
                statementSelect: ['出售', '过期', '损耗', '其他原因'],
                sels: [],//列表选中列
                userName: '',
                table: {
                    loading: false,
                    page: 1,
                    size: 10,
                    data: []
                },
                form: {
                    title: '',
                    addVisible: false,//入库界面是否显示
                    delVisible: false,//出库界面是否显示
                    maxNumber: '',//出库最大值
                    loading: false,
                    btnText: '提交',
                    data: {
                        id: '',
                        drugId: '',
                        drugName: '',
                        productionTime: '',
                        deadline: '',
                        price: '',
                        type: '',
                        number: '',
                        statement: '',
                        creator: '',
                        createTime: '',
                    },
                    range: '',
                    rules: {}
                }
            };
        },
        computed: {
            drugList: function () {
                var self = this, data = self.table.data, page = self.table.page, size = self.table.size,
                    keyword = self.filters.keyword;
                var v = data.filter(function (p) {
                    return (p.name && p.name.indexOf(keyword) !== -1)
                        || (p.manufacturers && p.manufacturers.indexOf(keyword) !== -1)
                        || (p.creator && p.creator.indexOf(keyword) !== -1)
                        || (p.updateUser && p.updateUser.indexOf(keyword) !== -1)
                });
                v = v.slice((page - 1) * size, page * size);
                return v;
            }
        },
        methods: {
            getDrug() {
                this.table.loading = true;
                getDrugList().then((data) => {
                    this.table.data = data;
                    this.table.loading = false;
                })
            },
            selsChange: function (sels) {
                this.sels = sels;
            },
            handleCurrentChange(val) {
                this.table.page = val;
            },
            handleSizeChange(val) {
                this.table.size = val;
            },
            handleChange: function (value) {
                let splits = value.split("至");
                this.form.data.productionTime = splits[0];
                this.form.data.deadline = splits[1];
            },
            add: function (index, row) {
                this.form.addVisible = true;
                this.form.title = '入库';
                this.form.data.id = '';
                this.form.data.drugId = row.id;
                this.form.data.drugName = row.name;
                this.form.data.productionTime = '';
                this.form.data.deadline = '';
                this.form.data.price = '';
                this.form.data.type = 0;
                this.form.data.number = '';
                this.form.data.statement = '';
                this.form.data.creator = this.userName;
                this.form.data.createTime = '';
            },
            del: function (index, row) {
                this.form.maxNumber = row.number;
                this.form.delVisible = true;
                this.form.title = '出库';
                this.form.data.id = '';
                this.form.data.drugId = row.id;
                this.form.data.drugName = row.name;
                this.form.data.productionTime = '';
                this.form.data.deadline = '';
                this.form.data.price = '';
                this.form.data.type = 1;
                this.form.data.number = '';
                this.form.data.statement = '';
                this.form.data.creator = this.userName;
                this.form.data.createTime = '';
            },
            submit: function () {
                this.$refs.form.validate((valid) => {
                    if (valid) {
                        this.$confirm('确认提交吗?', '提示', {}).then(() => {
                            this.form.loading = true;
                            this.form.btnText = '提交中';
                            var formData = this.form.data;
                            let param = Object.assign({}, formData);
                            if (formData.type === 0) {
                                addDrugInventory(param).then((data) => {
                                    let {message, type} = data;
                                    this.$message({
                                        showClose: true,
                                        message: message,
                                        type: type
                                    });
                                    this.getDrug();
                                });
                            } else if (formData.type === 1) {
                                removeDrugInventory(param).then((data) => {
                                    let {message, type} = data;
                                    this.$message({
                                        showClose: true,
                                        message: message,
                                        type: type
                                    });
                                    this.getDrug();
                                });
                            }
                            this.form.loading = false;
                            this.form.btnText = '提交';
                            this.form.addVisible = false;
                            this.form.delVisible = false;
                        });
                    }
                });
            },
            //获取当前用户名
            getUserName: function () {
                var user = sessionStorage.getItem('user');
                if (user) {
                    user = JSON.parse(user);
                    this.userName = user.account;
                }
            }
        },
        mounted() {
            this.getUserName();
            this.getDrug();
        }
    }

</script>


