export const getFeedback = (params) => {
    return axios.get(`${base}/feedback/list?type=` + params).then(res => res.data);
};

export const addFeedback = params => {
    return axios.post(`${base}/feedback/add`, params, {headers: header}).then(res => res.data);
};

